<template>
  <div class="Boats-init plr">
    <div class="Boats-text">
      Take a disposition.
      &nbsp;<router-link to="/rules/#ships">Rules</router-link>
      &nbsp;<button class="small grey" @click="autoDisposition">Auto</button>
    </div>
    <div class="Boats">
      <div class="Boats-group"
           :class="boat4"><div class="Boats-boat" :class="boat40">
        <div class="Boats-boat__cell">
        </div><div class="Boats-boat__cell">
        </div><div class="Boats-boat__cell">
        </div><div class="Boats-boat__cell">
        </div>
        </div>
      </div><div class="Boats-group"
           :class="boat3"><div class="Boats-boat" :class="boat30">
        <div class="Boats-boat__cell">
        </div><div class="Boats-boat__cell">
      </div><div class="Boats-boat__cell">
      </div>
      </div><div class="Boats-boat" :class="boat31">
        <div class="Boats-boat__cell">
        </div><div class="Boats-boat__cell">
      </div><div class="Boats-boat__cell">
      </div>
      </div></div><div class="Boats-group"
           :class="boat2"><div class="Boats-boat" :class="boat20">
        <div class="Boats-boat__cell">
        </div><div class="Boats-boat__cell">
      </div>
      </div><div class="Boats-boat" :class="boat21">
        <div class="Boats-boat__cell">
        </div><div class="Boats-boat__cell">
      </div>
      </div><div class="Boats-boat" :class="boat22">
        <div class="Boats-boat__cell">
        </div><div class="Boats-boat__cell">
      </div>
      </div></div><div class="Boats-group"
           :class="boat1">
      <div class="Boats-boat" :class="boat10">
        <div class="Boats-boat__cell"></div>
      </div><div class="Boats-boat" :class="boat11">
      <div class="Boats-boat__cell"></div>
      </div><div class="Boats-boat" :class="boat12">
        <div class="Boats-boat__cell"></div>
      </div><div class="Boats-boat" :class="boat13">
        <div class="Boats-boat__cell"></div>
      </div></div>
    </div>
    <transition name="showBlock">
      <div class="Boats-text-finish" v-show="disposition">
        <button class="small red" @click="finishDisposition">Ready?</button>
        &nbsp;
        <button class="small grey" @click="clearDisposition">Clear</button>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'Boats',
  data () {
    return {
      boat: this.$store.state.game.players[this.$store.state.user._id].boat,
      boat10: this.boa(1, 0),
      boat11: this.boa(1, 1),
      boat12: this.boa(1, 2),
      boat13: this.boa(1, 3),
      boat20: this.boa(2, 0),
      boat21: this.boa(2, 1),
      boat22: this.boa(2, 2),
      boat30: this.boa(3, 0),
      boat31: this.boa(3, 1),
      boat40: this.boa(4, 0),
      boat1: this.group(1),
      boat2: this.group(2),
      boat3: this.group(3),
      boat4: this.group(4)
    }
  },
  computed: {
    boatcounter: function () {
      return this.$store.state.game.players[this.$store.state.user._id].boat.c
    },
    disposition: function () {
      return this.$store.state.game.players[this.$store.state.user._id].boat.ok
    }
  },
  methods: {
    boa: function (size, index) {
      let boat = this.$store.state.game.players[this.$store.state.user._id].boat
      return (boat.n[size][1] > (index)) ? 'done' : ''
    },
    group: function (size) { // if length of group more then due
      let boat = this.$store.state.game.players[this.$store.state.user._id].boat
      return (boat.n[size][1] > boat.n[size][0]) ? 'wrong' : ''
    },
    autoDisposition: function () {
      this.$store.dispatch('autoDisposition')
    },
    finishDisposition: function () {
      this.$store.dispatch('finishDisposition')
    },
    clearDisposition: function () {
      this.$store.dispatch('clearDisposition')
    }
  },
  watch: {
    boatcounter () {
      this.boat10 = this.boa(1, 0)
      this.boat11 = this.boa(1, 1)
      this.boat12 = this.boa(1, 2)
      this.boat13 = this.boa(1, 3)
      this.boat20 = this.boa(2, 0)
      this.boat21 = this.boa(2, 1)
      this.boat22 = this.boa(2, 2)
      this.boat30 = this.boa(3, 0)
      this.boat31 = this.boa(3, 1)
      this.boat40 = this.boa(4, 0)
      this.boat1 = this.group(1)
      this.boat2 = this.group(2)
      this.boat3 = this.group(3)
      this.boat4 = this.group(4)
    }
  }
}
</script>

<style lang="sass">
  .Boats-init
    text-align: center;

  .land2 .Boats-init
    margin-top: 1em;

  .Boats-text
    margin: .5em 0;

  .Boats
    margin: 0 0 .5em;
    padding: 0 0 0 15px;

  .Boats-boat
    display: inline-block;
    margin: 0 5px 0 0;

  .Boats-group
    display: inline-block;
    margin: 0 10px 0 0;

  .Boats-boat__cell
    display: inline-block;
    width: 5px;
    height: 5px;
    border: 1px solid #ccc;
    margin: 0 2px 0 0;

  .done .Boats-boat__cell
    background: $blue;
    border: 1px solid $blue;

  .wrong .Boats-boat__cell
    background: $red;
    border: 1px solid $red;

</style>



