<template>
  <div id="App" :class="appClass">
    <bar :title="title"/>
    <div class="mainview" :style="mainStyle">
      <transition :name="transitionName">
          <router-view/>
      </transition>
    </div>
  </div>
</template>

<script>
import Bar from '@/components/Bar'

export default {
  name: 'App',
  data () {
    return {
      transitionName: 'slide-left'
    }
  },
  computed: {
    home: function () {
      return (this.$route.path === '/')
    },
    title: function () {
      return this.$route.name
    },
    appClass: function () {
      return 'land' + this.$store.state.window.land +
        ' Bar-menu-panel-open' + this.$store.state.barMenuOpened * 1 +
        ((this.home) ? ' home' : '')
    },
    mainStyle: function () {
      return 'height:' + this.$store.state.window.mainHeight + 'px;'
    }
  },
  components: {Bar},
  methods: {
    windowResized () {
      this.$store.dispatch('windowResized')
    },
    closeMenu () {
      this.$store.dispatch('barMenuOpen', false)
    },
    tabFocus () {
      this.$store.commit('setTab', true)
    },
    tabBlur () {
      this.$store.commit('setTab', false)
    }
  },
  watch: {
    '$route' (to, from) {
      this.closeMenu()
      const toDepth = to.path.split('/').length
      const fromDepth = from.path.split('/').length
      this.transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left'
    }
  },
  mounted () {
    this.$nextTick(function () {
      window.addEventListener('resize', this.windowResized)
      this.windowResized()
      window.addEventListener('focus', this.tabFocus)
      window.addEventListener('blur', this.tabBlur)
      let appStart = document.getElementById('app-start')
      if (appStart) {
        appStart.className = 'out'
        setTimeout(function () {
          appStart.className = 'del'
        }, 3000)
        appStart.parentNode.removeChild(appStart)
      }
    })
  },
  beforeCreate () {
    this.$store.commit('setSocket', this.$socket)
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.windowResized)
    window.removeEventListener('focus', this.tabFocus)
    window.removeEventListener('blur', this.tabBlur)
  }
}
</script>
<style lang="sass">
  *
    -webkit-touch-callout: none;                /* prevent callout to copy image, etc when tap to hold */
    -webkit-text-size-adjust: none;             /* prevent webkit from resizing text to fit */
    -webkit-tap-highlight-color: rgba(0,0,0,0); /* prevent tap highlight color / shadow */
    line-height: 1.2em;

  html, body
    overflow-x: hidden;

  #App
    font-family: 'Karma';
    position: relative;
    width: 100vw;
    min-height: 100vh;
    box-sizing: border-box;
    transition: all .5s ease;
    overflow-x: hidden;

  a
    color: $blue;

  .land0
    // padding-bottom: 50vh;

  .mainview
    position: relative;

  .slide-left-enter-active, .slide-left-leave-active, .slide-right-enter-active, .slide-right-leave-active
    transition: all .5s ease-in-out;
    position: absolute;
    top: 0;
    background: #fff;
    width: 100vw;
    min-height: 100vh;
    transform: translateX(0);

  .slide-left-enter-active, .slide-right-enter-active
    z-index: 99;

  .slide-left-leave-active:after, .slide-right-leave-active:after
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    min-height: 100%;
    z-index: 98;
    background: rgba(60,120,120,.7);
    opacity: 0;
    transition: all .5s ease;

  .slide-left-enter
    transform: translateX(100%);

  .slide-right-enter
    transform: translateX(-100%);

  .slide-left-leave-to:after, .slide-right-leave-to:after
    opacity: 1;

  .plr
    padding-left: 4vw;
    padding-right: 4vw;

  @media screen and (min-width: 1020px)
    .plr
      padding-left: 30px;
      padding-right: 30px;

  .form
    box-sizing: border-box;
    max-width: 320px;
    margin-left: auto;
    margin-right: auto;

  .form-row
    margin-bottom: 1em;

  input
    width: 100%
    box-sizing: border-box;
    padding: .6em 1em .3em;
    font-weight: 600;
    border-radius: 3px;
    border: 1px solid #ccc;
    transition: all .1s ease;
    outline: none !important;

  .button
    display: inline-block;
    width: 100%;
    border-radius: 3px;
    background: $blue;
    box-sizing: border-box;
    padding: .7em 1em .4em;
    color: #fff;
    text-align: center;
    text-decoration: none;
    font-weight: 700;
    cursor: pointer;
    user-select: none;
    transform-origin: 50% 50%;
    transition: all .1s ease;
    outline: none !important;

  input:active, button:active, .button:active
    transform: scale(.9);

  label
    font-size: .9em;
    color: #666;
    display: block;

  table
    border-collapse: collapse;

  th
    background: $grey2;

  td, th
    border: 1px solid #ddd;
    padding: .5em;

  .center
    text-align: center;

  button.small
    display: inline-block;
    width: auto;
    border: 0 none;
    border-radius: 3px;
    box-sizing: border-box;
    padding: .4em 1em .2em;
    cursor: pointer;
    user-select: none;
    transform-origin: 50% 50%;
    transition: all .1s ease;
    background: $grey2;
    outline: none !important;

  button.yellow, .yellow
    background: $yellow;
    color: #000;

  button.red, .red
    background: $red;
    color: $white;
    font-weight: 700;

  button.grey, .grey
    background: $grey2;
    color: #000;

  .showBlock-enter-active, .showBlock-leave-active
    transition: all .5s ease;
    overflow: hidden;

  .showBlock-leave, .showBlock-enter-to
    max-height: 100vh;
    opacity: 1;

  .showBlock-enter, .showBlock-leave-to
    max-height: 0;
    opacity: 0;

</style>



