<template>
  <div v-if="type === 'CellCol'" class="Cell Board-row-a__cell" :style="cellWidth">{{ content }}</div>
  <div v-else-if="type === 'CellRow'" class="Cell Board-row-1__cell" :style="cellWidth">{{ content }}</div>
  <div v-else-if="type === 'CellR'" class="Cell Board__right-padding" :style="cellWidth"></div>
  <div v-else-if="type === 'Cell'" class="Cell" :class="boatClassProp" @click="cell" :style="cellWidth"></div>
</template>

<script>
export default {
  name: 'Cell',
  data () {
    return {
      boatClassProp: ''
    }
  },
  computed: {
    cellWidth: function () {
      return 'width:' + this.$store.state.window.cellWidth + 'px; height:' + this.$store.state.window.cellWidth + 'px;'
    },
    boatcounter: function () {
      return (this.type === 'Cell') ? this.$store.state.game.players[this.playerid].boat.c : false
    },
    disposition: function () {
      return (this.$store.state.game.players[this.$store.state.user._id].phase === 'disposition')
    },
    ready: function () {
      return ((this.$store.state.game.players[this.$store.state.players[0]].phase === 'ready') && (this.$store.state.game.players[this.$store.state.players[1]].phase === 'ready'))
    },
    turn: function () {
      return (this.$store.state.game.turn === this.$store.state.user._id)
    }
  },
  props: ['row', 'col', 'type', 'content', 'player', 'playerid'],
  methods: {
    boatClass: function () { // disposition
      let ret = ''
      let boat = this.$store.state.game.players[this.playerid].board[this.player][this.row][this.col].boat
      let boats = this.$store.state.game.players[this.playerid].boat
      if (boat) {
        if (boat[0] > 4) { // if boat bigger than 4-decks
          ret = 'boat-wrong'
        } else {
          let n = boats['n'][boat[0]]
          if (n && (n[1] > n[0])) { // if length of group more then due
            ret = 'boat-wrong'
          }
        }
      }
      return ret
    },
    cell () {
      if ((this.player === 0) && this.disposition) {
        this.$store.dispatch('disposition', [+this.row, +this.col])
      } else if ((this.player === 1) && this.ready && this.turn) {
        this.$store.dispatch('move', [+this.row, +this.col])
      }
    }
  },
  watch: {
    boatcounter () {
      if (this.disposition && (this.type === 'Cell')) {
        this.boatClassProp = this.boatClass()
      }
    }
  }
}
</script>

<style lang="sass" >
  .Cell
    display: inline-block;
    vertical-align: top;
    width: 9%;
    height: 9%;
    border: 2px solid $white;
    box-sizing: border-box;
    background: $grey1;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    margin: -2px 0 0 -2px;
    transition: all .3s ease;

  .Board__right-padding
    background: inherit;
    border: none;

  .Cell.boat
    background: $blue;

  .boat-wrong
    background: $red !important;

  .Cell.fire
    background: 50% 50% $red;
    background-image: $img-x;
    background-size: cover;
    animation: cell-tap .5s;

  .Cell.fire:before
    content: '';
    position: absolute;
    width: 100%;
    min-height: 100%;
    left: 0;
    border-radius: 300%;
    animation: cell-boat1 1s infinite linear;

  @keyframes cell-boat
    0%
      transform: scale(1.5);
      box-shadow: 0 0 0 .01vw #ff0 inset;
    100%
      transform: scale(.01);
      box-shadow: 0 0 0 4vw #ff0 inset;

  .Cell.dead
    background: 50% 50% #a6afb9;
    background-image: $img-x;
    background-size: cover;
    animation: cell-tap .5s;

  .Cell.disable
    animation: cell-disable 1s .1s forwards ease;

  @keyframes cell-disable
    100%
      background: #ddd;

  .Cell.missed
    background: #b6e1e6;
    animation: cell-tap .5s;

  .Cell.loading
    animation: cell-tap .5s;

  .Cell.loading:before, .Cell.loading:after
    content: '';
    position: absolute;
    width: 100%;
    min-height: 100%;
    left: 0;
    border-radius: 300%;
    background: $yellow;
    opacity: 0;
    animation: cell-loading 2s infinite ease;

  .Cell.loading:after
    animation-delay: .3s;
    background: $white;

  @keyframes cell-loading
    0%
      transform: scale(0);
      opacity: 1;
    100%
      transform: scale(2);
      opacity: 0;

  @keyframes cell-tap
    0%,100%
      transform: scale(1);
    50%
      transform: scale(.7);

  .Board
    display: inline-block;
    padding: .3em 0 1.5em 0;
    text-align: center;
    max-width: 500px;
    width: 100%;
    position: relative;
    box-sizing: border-box;
    margin-top: 2px;

  .Board-row
    white-space: nowrap;

  .Cell.Board-row-a__cell, .Cell.Board-row-1__cell
    text-transform: uppercase;
    font-size: 60%;
    color: rgba(0,0,0,.5);
    padding-top: 3%;
    z-index: 2;
    background: none;
    border: none;

  .Board.player1 .Cell.Board-row-a__cell, .Board.player1 .Cell.Board-row-1__cell
    background: none;
    border: none;

  .turn-board .TurnIcon:before
    left: 30%;
    top: 0;
    position: absolute;
    content: '';
    width: 40%;
    min-height: 70%;
    background: 50% 100% no-repeat;
    background-image: $img-duck;
    background-size: 100%;
    animation: wave-icon-duck 4s infinite linear;
    transform-origin: 50% 80%;

  .turn-board .TurnIcon:after
    left: 20%;
    top: 0;
    position: absolute;
    content: '';
    width: 60%;
    min-height: 80%;
    background: 0% 100% repeat-x;
    background-image: $img-wave;
    background-size: 40%;
    animation: wave-icon 1s infinite linear;

  .turn-board.player1 .TurnIcon:after
    background-image: $img-wave-red;

</style>



