<template>
  <div>
    <div class="Rules plr">
      <h2>Battleship</h2>
      <p>
        Battleship (also Battleships or Sea Battle) is a guessing game for two players. It is played on ruled grids (paper or board) on which the players' fleets of ships (including battleships) are marked. The locations of the fleet are concealed from the other player. Players alternate turns calling "shots" at the other player's ships, and the objective of the game is to destroy the opposing player's fleet.
      </p>
      <p>
        Battleship is known worldwide as a pencil and paper game which dates from World War I. It was published by various companies as a pad-and-pencil game in the 1930s, and was released as a plastic board game by Milton Bradley in 1967. The game has spawned electronic versions, video games, smart device apps and a film.
      </p>
      <h2 id="ships">Ships</h2>
      <table>
        <tr>
          <th>
            Type of ship
          </th>
          <th>
            Quantity
          </th>
        </tr>
        <tr>
          <td>
            battleship (four-decker)
          </td>
          <td class="center">
            1
          </td>
        </tr>
        <tr>
          <td>
            cruiser (three-decker)
          </td>
          <td class="center">
            2
          </td>
        </tr>
        <tr>
          <td>
            destroyer (two-decker)
          </td>
          <td class="center">
            3
          </td>
        </tr>
        <tr>
          <td>
            submarine (single-decker)
          </td>
          <td class="center">
            4
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Rules'
}
</script>
<style lang="sass">
  .Rules
    padding-top: 1em;
    padding-bottom: 1em;

</style>



