<template>
  <div>
    <nav class="nav-boarduser">
      <transition-group>
        <div class="nav-boarduser__butto p0" key="0" :class="{ active: currentBoard(0) }">
          <div class="nav-boarduser__button" @click="switchBoard(0)">{{ playername0 }}</div>
          <div class="nav-boarduser__life" :style="lifeStyle(0)"></div>
        </div><div class="nav-boarduser__vs" :class="turnClass" key="2">vs
        </div><div class="nav-boarduser__butto p1" key="1" :class="{ active: currentBoard(1) }">
          <div class="nav-boarduser__button" @click="switchBoard(1)">{{ playername1 }}</div>
          <div class="nav-boarduser__life" :style="lifeStyle(1)"></div>
        </div>
      </transition-group>
    </nav>
    <texts></texts>
    <transition name="showBlock">
      <boats v-if="disposition"></boats>
    </transition>
    <flickity ref="flickity" class="boarduser-boards" :options="flickityOptions" :style="boardWidth" :class="{ disposition: disposition }">
      <board  v-for="(player, i) in players" :key="i" :player="i" :playerid="player"></board>
    </flickity>
  </div>
</template>
<script>
import Texts from '@/components/Texts'
import Boats from '@/components/Boats'
import Board from '@/components/Board'
import Flickity from 'vue-flickity'

export default {
  name: 'Basic',
  data () {
    return {
      players: false,
      playername0: false,
      playername1: false,
      flickityOptions: {
        prevNextButtons: false,
        pageDots: false,
        dragThreshold: 10,
        draggable: false
      }
    }
  },
  computed: {
    disposition: function () {
      return (this.$store.state.game.players[this.$store.state.user._id].phase === 'disposition')
    },
    phase: function () {
      return this.$store.state.game.players[this.$store.state.user._id].phase
    },
    boardWidth: function () {
      return 'width:' + this.$store.state.window.boardWidth + 'px; height:' + this.$store.state.window.boardWidth + 'px;'
    },
    land: function () {
      return this.$store.state.window.land
    },
    version: function () {
      return this.$store.state.game.version
    },
    ready: function () {
      return ((this.$store.state.game.players[this.$store.state.players[0]].phase === 'ready') && (this.$store.state.game.players[this.$store.state.players[1]].phase === 'ready'))
    },
    turnClass: function () {
      let ret = ''
      if (this.ready) {
        if (this.turn(0)) {
          ret = 'p0'
        } else if (this.turn(1)) {
          ret = 'p1'
        }
      }
      return ret
    }
  },
  methods: {
    lifeStyle: function (n) {
      let style = ''
      if (this.ready) {
        let life = this.$store.state.game.players[this.$store.state.players[n]].boat.c
        style = 'width:' + life + '%;'
        if (life < 40) {
          style += 'background:#ff4040;'
          /*
          if (life === 0) {
            this.$store.dispatch('endGame', n)
          }
          */
        }
      }
      return style
    },
    turn: function (n) {
      return (this.$store.state.game.turn === this.$store.state.players[n])
    },
    currentBoard: function (i) {
      return (this.$store.state.currentBoard === i)
    },
    switchBoard (n) {
      if (!this.disposition) {
        if (!this.currentBoard(n)) {
          this.$refs.flickity.select(n)
        }
      }
    },
    selectBoard () {
      let that = this
      this.$refs.flickity.on('select', function () {
        that.$store.dispatch('selectBoard', this.selectedIndex)
      })
    },
    watchPhase () {
      if (this.land !== 2) { // on land2 flc is off
        if (!this.disposition) { // no draggable on disposition
          let flc = typeof this.$refs.flickity
          if (flc !== 'undefined') {
            this.$refs.flickity.destroy()
            this.flickityOptions.draggable = true
            this.$refs.flickity.init()
          }
          this.selectBoard()
        }
      }
    },
    watchLand (n) {
      let flc = typeof this.$refs.flickity
      if ((n === 2) && (flc !== 'undefined')) {
        this.$refs.flickity.destroy()
      } else if ((n !== 2) && (flc === 'undefined')) {
        this.watchPhase()
      }
    },
    watchVersion () {
      this.playername0 = this.$store.state.game.players[this.$store.state.players[0]].user.name
      this.playername1 = this.$store.state.game.players[this.$store.state.players[1]].user.name
      this.players = this.$store.state.players
      let currentBoard = this.$store.state.currentBoard
      let newBoard = 1 - this.$store.state.currentBoard
      if (this.turn(currentBoard)) {
        let that = this
        setTimeout(function () {
          that.switchBoard(newBoard)
        }, 2000)
      }
    }
  },
  mounted () {
    this.$nextTick(function () {
      this.watchPhase()
    })
  },
  created () {
    this.watchVersion()
  },
  watch: {
    land (n) {
      this.watchLand(n)
    },
    phase () {
      this.watchPhase()
    },
    version () {
      this.watchVersion()
    }
  },
  components: {
    Texts,
    Boats,
    Board,
    Flickity
  }
}
</script>
<style lang="sass">
  .nav-boarduser
    text-align: center;
    padding: 0 1rem;
    box-sizing: border-box;
    margin: .7em auto 0;
    max-width: 1020px;

  .nav-boarduser__butto
    display: inline-block;
    color: #999;
    white-space: nowrap;
    user-select: none;
    position: relative;
    width: calc(50% - 1.2rem);
    text-align: left;
    opacity: .3;
    transform-origin: 50% 50%;
    transition: all .2s ease;

  .nav-boarduser__butto.p0
    text-align: right;

  .nav-boarduser__button
    overflow: hidden;
    text-overflow: ellipsis;
    color: #000;
    cursor: pointer;
    white-space: nowrap;
    user-select: none;
    border-bottom: 3px solid #ddd;

  .nav-boarduser__life
    position: absolute;
    width: 100%;
    height: 3px;
    bottom: 0;
    right: 0;
    background: $blue;
    transition: all .5s ease;

  .p1 .nav-boarduser__life
    right: auto;
    left: 0;

  .nav-boarduser__butto:active
    transform: scale(.8);

  .nav-boarduser__butto.active
    opacity: 1;

  .nav-boarduser__vs
    display: inline-block;
    width: 2.4rem;
    color: #999;
    font-size: .5em;
    font-style: italic;
    vertical-align: top;
    line-height: 1.4rem;
    position: relative;

  .nav-boarduser__vs.p0, .nav-boarduser__vs.p1
    color: #fff;

  .nav-boarduser__vs.p0:before, .nav-boarduser__vs.p1:after
    content: '';
    position: absolute;
    width: 100%;
    height: .8rem;
    background: $img-cannon no-repeat 30% 50%;
    background-size: contain;
    opacity: 1;
    top: .2rem;
    left: 50%;
    transform: translateX(-50%);
    transition: all 1s ease;

  .nav-boarduser__vs.p1:after
    transform: translateX(-50%) scale(-1, 1);
    filter: grayscale(1);
    opacity: .5;

  .boarduser-boards
    position: relative;
    margin: 0 auto;
    user-select: none;
    outline: none !important;
    text-align: center;
    white-space: nowrap;

  .flickity-viewport
    min-height: 100%;
    overflow: hidden;

  .land2 .nav-boarduser
    display: block;

  .land2 .boarduser-boards
    width: 100% !important;

  .land2 .Board+.Board
    margin-left: 20px;

  .land2 .boarduser-boards.disposition .Board.player1
    display: none;

  .nav-boarduser__butto.turn .nav-boarduser__button
    // background: $yellow;

</style>



