<template>
<div  class="Board" :class="'player' + player + ' playerid:' + playerid" :style="boardWidth">
  <div class="Board-row Board-row-a">
    <cell type="CellRow" class="TurnIcon">
    </cell><cell v-for="(item) in boardi.col" :key="item+0" type="CellCol" :content="item">
    </cell><cell type="CellR">
    </cell>
  </div>
  <div  v-for="(row, rowi) in boardi.row" class="Board-row" :key="rowi">
    <cell type="CellRow" :content="row">
    </cell><cell v-for="(item, coli) in boardi.col" type="Cell" :key="item+row" :class="board[rowi][coli].class" :row="rowi" :col="coli" :player="player" :playerid="playerid">
    </cell><cell type="CellR">
    </cell>
  </div>
</div>
</template>

<script>
import Cell from '@/components/Cell'
export default {
  name: 'Board',
  data () {
    return {
      boardi: this.$store.state.boardi,
      board: false
    }
  },
  computed: {
    boardWidth: function () {
      return 'max-width:' + this.$store.state.window.boardWidth + 'px'
    },
    version: function () {
      return this.$store.state.game.version
    }
  },
  methods: {
    watchVersion () {
      this.board = this.$store.state.game.players[this.playerid].board[this.player]
    }
  },
  props: ['player', 'playerid'],
  components: {Cell},
  created () {
    this.watchVersion()
  },
  watch: {
    version () {
      this.watchVersion()
    }
  }
}
</script>



