<template>
  <div>
    <div class="form Game-form plr">
      <transition-group name="showBlock">
        <div v-show="gameJoined" class="Game-joined" key="joined1">
          <p>
            <b>{{gameJoined}}</b>
          </p>
        </div>
        <div v-if="!gameLinkCopied" class="Game-link" key="copy0">
          <p>
            Send this link to&nbsp;the second captain <nobr>({{ secondName }})</nobr> to&nbsp;join your game.
          </p>
          <div class="form-row">
            <label for="gameLink">Link</label>
            <input type="text" readonly v-model="gameLink" id="gameLink">
          </div>
          <a class="button" v-clipboard:copy="gameLink" v-clipboard:success="handleCopy">Copy link</a>
        </div>
        <div v-else class="Game-link-copied" key="copy1">
          <p>
            The link is copied.
          </p>
        </div>
      </transition-group>
      <div class="Game-new">
        <a class="button grey" @click="handleNew">New game</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Game',
  data () {
    return {
      gameLink: false,
      secondName: false,
      gameLinkCopied: false,
      gameJoined: false
    }
  },
  computed: {
    gameId: function () {
      return this.$store.state.game._id
    },
    version: function () {
      return this.$store.state.game.version
    }
  },
  methods: {
    gameLinkF (id) {
      return location.origin + '/#/g/' + id
    },
    secondNameF () {
      return (this.$store.state.game.players[this.$store.state.players[1]].user) ? this.$store.state.game.players[this.$store.state.players[1]].user.name : ''
    },
    handleCopy (e) {
      this.gameLinkCopied = true
    },
    handleNew () {
      this.gameLinkCopied = false
      this.$store.dispatch('newGame')
    },
    watchVersion () {
      this.gameJoined = (+this.$store.state.players[1] !== 0) ? 'Both captains are joined.' : this.gameJoined
      this.gameLink = this.gameLinkF(this.$store.state.game._id)
      this.secondName = this.secondNameF()
    }
  },
  beforeCreate () {
    let gameId = this.$route.params.gameId
    if (typeof gameId !== 'undefined') {
      this.$store.dispatch('joinGame', gameId).then(response => {
        this.gameJoined = 'Connecting, please wait...'
      })
    } else {
      this.$store.dispatch('saveGame')
    }
  },
  created () {
    this.watchVersion()
  },
  watch: {
    gameId (id) {
      this.gameLink = this.gameLinkF(id)
      this.secondName = this.secondNameF()
    },
    version () {
      this.watchVersion()
    }
  }
}
</script>
<style lang="sass">
  .Game-form
    padding-top: 1em;

  .Game-new
    padding-top: 1em;

</style>



