<template>
  <div>
    <div class="Bar plr" id="Bar">
      <router-link to="/" class="Bar__logo"></router-link>
      <div class="Bar-title">{{ title }}</div>
      <div class="Bar-menu">
        <div class="Bar-menu__icon" @click="switchMenu()"></div>
      </div>
    </div>
    <flickity class="Bar-menu-panel-flickity" ref="flickity" :options="flickityOptions" :style="flickityStyle">
      <div class="Bar-menu-panel plr">
        <router-link to="/user/" class="Bar-menu__item">{{ captain }}</router-link>
        <router-link to="/game/" class="Bar-menu__item">Game</router-link>
        <router-link to="/messages/" class="Bar-menu__item">Messages</router-link>
        <router-link to="/rules/" class="Bar-menu__item">Rules</router-link>
      </div>
    </flickity>
  </div>
</template>
<script>
import Flickity from 'vue-flickity'
export default {
  name: 'Bar',
  data () {
    return {
      flickityOptions: {
        draggable: true,
        freeScroll: true,
        contain: true,
        prevNextButtons: false,
        pageDots: false,
        cellAlign: 'left'
      },
      flickityVector: 'init'
    }
  },
  props: ['title'],
  computed: {
    captain: function () {
      return this.$store.state.user.name
    },
    flickityStyle: function () {
      if (this.flickityVector > 0) {
        let delta = 0.7 - this.flickityVector / this.$store.state.window.width * 0.7
        return 'background: rgba(60,120,120, ' + delta + ');'
      } else if (this.flickityVector === 'init') {
        return 'opacity:0;'
      } else {
        return ''
      }
    }
  },
  mounted () {
    this.$nextTick(function () {
      let that = this
      this.resetFlickityVector()
      this.$refs.flickity.on('dragMove', function (event, pointer, moveVector) {
        that.flickityVector = moveVector.x
      })
      this.$refs.flickity.on('dragEnd', function (event, pointer) {
        if (that.flickityVector > 0) {
          that.closeMenu()
        }
      })
      this.$refs.flickity.on('staticClick', function (event, pointer, cellElement, cellIndex) {
        if (typeof cellIndex === 'undefined') {
          that.closeMenu()
        }
      })
    })
  },
  methods: {
    switchMenu (isOpened) {
      this.$store.dispatch('barMenuOpen', isOpened)
      this.resetFlickityVector()
    },
    closeMenu () {
      this.switchMenu(false)
    },
    resetFlickityVector () {
      let that = this
      setTimeout(function () {
        that.flickityVector = 0
      }, 490)
    }
  },
  components: {
    Flickity
  }
}
</script>
<style lang="sass" >
  .Bar
    position: relative;
    background: #f0f0f0;
    padding-top: .6em;
    padding-bottom: .6em;
    border-bottom: 1px solid #ccc;

  .Bar-title
    text-align: center;
    line-height: 6vw;
    font-size: 2em;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 0 12vw;

  .home .Bar-title
    opacity: 0;

  .Bar__logo
    position: absolute;
    width: 6vw;
    height: 6vw;
    background: 100% 50% no-repeat;
    background-image: $img-logo;
    background-size: 10vw;
    transform-origin: 50% 50%;
    transition: all .1s ease;
    margin-left: 5vw;

  .Bar__logo:before
    content: '';
    width: 3vw;
    height: 6vw;
    background: #ccc;
    position: absolute;
    left: -5vw;
    background: 0% 50% no-repeat;
    background-image: $img-left;
    background-size: contain;

  .home .Bar__logo
    width: 10vw;
    margin-left: 0;

  .home .Bar__logo:before
    display: none;

  .Bar__logo:active
    transform: scale(.8);

  .Bar-menu
    position: absolute;
    top: .6em;
    right: 4vw;

  .Bar-menu__icon
    display: block;
    width: 6vw;
    height: 6vw;
    background: 50% 50% no-repeat;
    background-image: $img-helm;
    background-size: 100%;
    opacity: .8;
    cursor: pointer;
    transform-origin: 50% 50%;
    transition: all .2s ease;

  .Bar-menu__icon:active
    transform: scale(.8) rotate(20deg);

  @media screen and (max-width: 600px)
    .land0 .Bar__logo, .land0 .Bar-menu__icon
      width: 9vw;
      height: 9vw;

    .land0 .Bar-title
      line-height: 9vw;
      font-size: 1.2em;
      padding: 0 15vw;

    .land0 .Bar__logo
      background-size: 15vw;

    .land0.home .Bar__logo
      width: 15vw;

  @media screen and (min-width: 1020px)
    .Bar__logo, .Bar-menu__icon
      width: 60px;
      height: 60px;

    .Bar-title
      line-height: 60px;
      font-size: 2.4em;

    .Bar__logo
      background-size: 100px;
      margin-left: 50px;

    .Bar__logo:before
      width: 30px;
      height: 60px;
      left: -50px;

    .home .Bar__logo
      width: 100px;
      margin-left: 0;

    .Bar-menu
      right: 30px;

  .Bar-menu-panel
    box-sizing: border-box;
    padding-top: 1em;
    padding-bottom: .5em;
    background: #fff;
    width: 300vw;
    min-height: 100vh;
    left: calc(100vw - 280px) !important;

  .Bar-menu__item
    display: block;
    margin-bottom: .5em;
    text-decoration: none;
    color: #000;
    font-weight: 500;
    transform-origin: 0% 50%;
    transition: all .2s ease;

  .Bar-menu__item:active
    transform: scale(.8);

  .Bar-menu__item.router-link-active
    color: #999;
    text-decoration: underline;

  .Bar-menu-panel-flickity
    position: fixed;
    width: 100vw;
    top: 0;
    right: 0;
    user-select: none;
    outline: none !important;
    background: rgba(60,120,120,.7);
    transition: background .7s ease;
    z-index: 99;

  .Bar-menu-panel-flickity .flickity-viewport
    height: 100vh !important;
    overflow-y: auto;
    overflow-x: hidden;

  .Bar-menu-panel-open1 .Bar-menu-panel-flickity
    animation: bar-menu-panel-in1 .5s ease forwards;

  @keyframes bar-menu-panel-in1
    0%
      background: rgba(60,120,120,0);

  .Bar-menu-panel-open1 .Bar-menu-panel-flickity .flickity-slider
    animation: bar-menu-panel-in .5s ease forwards;

  @keyframes bar-menu-panel-in
    0%
      transform: translateX(100%);

  .Bar-menu-panel-open0 .Bar-menu-panel-flickity
    animation: bar-menu-panel-out .1s ease .5s forwards, bar-menu-panel-out1 .5s ease forwards;

  @keyframes bar-menu-panel-out
    100%
      right: 100vw;

  @keyframes bar-menu-panel-out1
    100%
      background: rgba(60,120,120,0);

  .Bar-menu-panel-open0 .Bar-menu-panel-flickity .flickity-slider
    animation: bar-menu-panel-out2 .5s ease forwards;

  @keyframes bar-menu-panel-out2
    100%
      transform: translateX(100%);

</style>



